  color: #333;
  font-family: 'Microsoft YaHei', 'Helvetica Neue', Arial, sans-serif;
  margin: 0px 0 12px;
  line-height: 1.4;
}

/*  */
.chart-subtitle {
  text-align: center;
  font-size: 14px;
  color: #666;
  white-space: pre-line;
  margin-bottom: 16px;
}

